import { useState, useEffect } from "react";
//Quill JS
import { useQuill } from "react-quilljs";
import "quill/dist/quill.snow.css";
import "./quill.css";

const QuillEditor = ({ id, placeholder, data, enable }) => {
   const [boolean, setBoolean] = useState(true);

   const modules = {
      toolbar: [
         ["bold", "italic", "underline", "strike"], // toggled buttons
         ["link", "image", "video"],

         [{ header: 1 }, { header: 2 }], // custom button values
         [{ list: "ordered" }, { list: "bullet" }],
         [{ indent: "-1" }, { indent: "+1" }], // outdent/indent

         [{ size: ["small", false, "large", "huge"] }], // custom dropdown
         [{ header: [1, 2, 3, 4, 5, 6, false] }],

         [{ color: [] }, { background: [] }], // dropdown with defaults from them
         [{ align: [] }],

         ["clean"],
      ],
   };

   const { quill, quillRef } = useQuill({ placeholder, modules });

   useEffect(() => {
      if (quill) {
         quill.enable(enable);
      }
   }, [quill, enable]);

   if (data && quill && boolean === true) {
      //console.log(data);
      quill.setContents(JSON.parse(data));
      setBoolean(false);
   } else {
      window.quill = quill;
   }

   return (
      <div id={id}>
         <div ref={quillRef} />
      </div>
   );
};

export default QuillEditor;
